import React from 'react';
import ReactPaginate from 'react-paginate';
import "./style.scss";

const PaginationBar = ({ setting, handleClick }) => {
  return (
    <ReactPaginate
      previousLabel={<i className="fas fa-angle-left" />}
      nextLabel={<i className="fas fa-angle-right" />}
      breakLabel='...'
      breakClassName={'page-item'}
      breakLinkClassName={'page-link'}
      pageCount={setting.totalPage}
      forcePage={setting.selected}
      marginPagesDisplayed={setting.marginPages || 1}
      pageRangeDisplayed={setting.pageRange || 1}
      onPageChange={handleClick}
      containerClassName={'pagination pagination-md'}
      subContainerClassName={'page-item'}
      activeClassName={'active'}
      pageLinkClassName={'page-link'}
      pageClassName={'page-item'}
      previousClassName={'page-item'}
      nextClassName={'page-item'}
      previousLinkClassName={'page-link'}
      nextLinkClassName={'page-link'}
      disabledClassName={'disabled'}
    />
  );
}

export default PaginationBar;
