import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import classNames from "classnames/bind";
import ReactDatetime from "react-datetime";
import { Link } from "react-router-dom";
import {
  Button,
  Card,
  Table,
  Row,
  Col,
  CardHeader,
  CardBody,
  Input,
  Label,
  ButtonGroup,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { getDate, getExportData } from "../../../helpers/UtilsHelper";
import {
  getList,
  updateStatus,
  triggerCronJob,
} from "../../../redux/actions/billingAction";
import { ExportToCsv } from "export-to-csv";
import PaginationBar from "../../../components/PaginationBar";
import Loading from "../../../components/Loading";
import cons from "../../../constants";
import BookingInvoice from "./BookingInvoice";
import "./ListInvoice.scss";
import Form from "reactstrap/lib/Form";

const defaultState = () => {
  return {
    dropdownStatusName: "Status",
    status: "",
    isLoading: true,
    isOpenSideBar: false,
    dropdownStatusOpen: false,
    checkVoidStatus: false,
    start_date: "",
    end_date: "",
    trigger_date: "",
    isDisabledTriggerBtn: true,
    validateTriggerDate: (current) => {
      var today = moment().subtract(0, "day");
      return current.isBefore(today);
    },
  };
};
const defaultFilters = () => {
  return {
    limit: 10,
    offset: 0,
    selected: 0,
    desc: false,
    status: "",
  };
};

class ListBookingInvoice extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState();
    this.filters = defaultFilters();
  }

  componentDidMount() {
    this.props.getList(this.filters);
  }

  handleChangeFilterStatus = (e, itemName) => {
    const { value, name } = e.target;
    if (name === "status") {
      if (Number(value) === cons.BILLING_STATUS.VOID) {
        this.setState({ checkVoidStatus: true });
      } else {
        this.setState({ checkVoidStatus: false });
      }
    }
    this.setState({ dropdownStatusName: itemName });
    this.filters[name] = value;
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  handleFilterVoidBilling = (e) => {
    const { checked } = e.target;
    if (checked) {
      this.filters.status = cons.BILLING_STATUS.VOID;
      this.setState({ dropdownStatusName: "Void created" });
    } else {
      this.filters.status = "";
      this.setState({ dropdownStatusName: "All Status" });
    }
    this.setState({ checkVoidStatus: checked });
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  handleSendInvoice = (id) => {
    alert(`send Invoices to: ${id}`);
    // this.props.sendInvoice()
  };

  handleUpdateStatus = (value, id) => {
    const status = value;
    this.props.updateStatus(status, id);
  };

  filter = () => {
    const { start_date, end_date } = this.state;
    this.filters = {
      ...this.filters,
      start_date: start_date ? getDate(start_date._d) : "",
      end_date: end_date ? getDate(end_date._d) : "",
    };
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  sort = (e) => {
    let name = e.target.attributes.name.value;
    if (name && name === this.filters.ordering) {
      this.filters.desc = !this.filters.desc;
    } else {
      this.filters.ordering = name;
      this.filters.desc = true;
    }
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  handleReactDatetimeChange = async (who, date) => {
    if (who === "trigger_date") {
      await this.setState({
        isDisabledTriggerBtn: !date,
        trigger_date: date,
      });
      this.filter();
    } else if (
      this.state.start_date &&
      who === "end_date" &&
      new Date(this.state.start_date._d + "") > new Date(date._d + "")
    ) {
      await this.setState({
        start_date: date,
        end_date: date,
      });
      this.filter();
    } else if (
      this.state.end_date &&
      who === "start_date" &&
      new Date(this.state.end_date._d + "") < new Date(date._d + "")
    ) {
      await this.setState({
        start_date: date,
        end_date: date,
      });
      this.filter();
    } else {
      await this.setState({
        [who]: date,
      });
      this.filter();
    }
  };

  getClassNameReactDatetimeDays = (date) => {
    if (this.state.start_date && this.state.end_date) {
    }
    if (
      this.state.start_date &&
      this.state.end_date &&
      this.state.start_date._d + "" !== this.state.end_date._d + ""
    ) {
      if (
        new Date(this.state.end_date._d + "") > new Date(date._d + "") &&
        new Date(this.state.start_date._d + "") < new Date(date._d + "")
      ) {
        return " middle-date";
      }
      if (this.state.end_date._d + "" === date._d + "") {
        return " end-date";
      }
      if (this.state.start_date._d + "" === date._d + "") {
        return " start-date";
      }
    }
    return "";
  };

  selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      this.resetFilterData();
      this.setState({ isOpenSideBar: false, tickCheckAll: false });
      this.filters.limit = value;
      this.props.getList(this.filters);
    }
  };

  selectPage = ({ selected }) => {
    this.filters.selected = selected || 0;
    this.filters.offset = Math.ceil(selected * this.filters.limit);
    this.setState({ isOpenSideBar: false, tickCheckAll: false });
    this.props.getList(this.filters);
  };

  resetFilterData = () => {
    this.filters.offset = 0;
    this.filters.selected = 0;
  };

  clearFilterData = () => {
    this.filters = defaultFilters();
    this.setState(defaultState());
    this.props.getList(this.filters);
  };

  toggleSelectStatus = () => {
    this.setState({ dropdownStatusOpen: !this.state.dropdownStatusOpen });
  };

  exportCSV = async () => {
    const tableData = this.props.bookingInvoiceData.result;
    const data = getExportData(
      tableData,
      cons.BILLING_EXPORT_CSV_CODE.ADMIN_BOOKING_INVOICE
    );
    const filename = String(moment().valueOf());
    const options = {
      filename,
      fieldSeparator: ",",
      quoteStrings: '"',
      decimalSeparator: ".",
      showLabels: true,
      showTitle: false,
      useTextFile: false,
      useBom: true,
      useKeysAsHeaders: true,
    };
    const csvExporter = new ExportToCsv(options);
    csvExporter.generateCsv(data);
  };

  triggerCronJobChargeInvoice = () => {
    this.props.triggerCronJob(
      String(moment(this.state.trigger_date).format("YYYY-MM-DD"))
    );
  };

  render() {
    const { dropdownStatusName, dropdownStatusOpen } = this.state;
    const { bookingInvoiceData, loading } = this.props;
    const bookingInvoiceArr = bookingInvoiceData.result;

    const { limit, selected } = this.filters;
    const totalPage = Math.ceil(bookingInvoiceData.count / limit) || 0;
    const paginationInfo = {
      from: selected * limit + 1,
      to:
        (selected + 1) * limit < bookingInvoiceData.count
          ? (selected + 1) * limit
          : bookingInvoiceData.count,
      total: bookingInvoiceData.count,
      getShowOne: function () {
        return this.from === this.to;
      },
    };
    const paginationSetting = {
      totalPage,
      selected: this.filters.selected,
    };
    const sortClass = {
      title: this.filters.ordering,
      value: this.filters.desc,
      get: function (name) {
        if (this.title === name) {
          return `sort ${this.value ? "sort-desc" : "sort-asc"}`;
        }
        return "sort";
      },
    };
    return (
      <>
        <Col className="list-invoice">
          <Card className="mt-3">
            {loading && <Loading />}
            <CardHeader className="bg-transparent border-0">
              <Row className="d-flex justify-content-between align-items-center">
                <Col
                  className="d-flex justify-content-center justify-content-lg-start"
                  xl={4}
                  lg={7}
                  xs={12}
                >
                  <Row className="align-items-center">
                    <Col xs={12} sm={5}>
                      <ReactDatetime
                        className="custom-date-input"
                        inputProps={{
                          placeholder: "-- start date --",
                        }}
                        closeOnSelect={true}
                        value={this.state.start_date}
                        timeFormat={false}
                        dateFormat={"MM/DD/YYYY"}
                        onChange={(e) =>
                          this.handleReactDatetimeChange("start_date", e)
                        }
                        renderDay={(props, currentDate, selectedDate) => {
                          let classes = props.className;
                          classes +=
                            this.getClassNameReactDatetimeDays(currentDate);
                          return (
                            <td {...props} className={classes}>
                              {currentDate.date()}
                            </td>
                          );
                        }}
                      ></ReactDatetime>
                    </Col>
                    <b className="d-sm-block d-none">-</b>
                    <Col xs={12} sm={5}>
                      <ReactDatetime
                        className="custom-date-input"
                        inputProps={{
                          placeholder: "-- end date --",
                        }}
                        closeOnSelect={true}
                        value={this.state.end_date}
                        timeFormat={false}
                        dateFormat={"MM/DD/YYYY"}
                        onChange={(e) =>
                          this.handleReactDatetimeChange("end_date", e)
                        }
                        renderDay={(props, currentDate, selectedDate) => {
                          let classes = props.className;
                          classes +=
                            this.getClassNameReactDatetimeDays(currentDate);
                          return (
                            <td {...props} className={classes}>
                              {currentDate.date()}
                            </td>
                          );
                        }}
                      ></ReactDatetime>
                    </Col>
                    <Col sm={1} className="d-sm-block d-none">
                      <i className="fas fa-calendar-alt"></i>
                    </Col>
                  </Row>
                </Col>
                <Col
                  className="d-flex justify-content-center justify-content-lg-start"
                  xl={4}
                  lg={6}
                  xs={12}
                >
                  <Row className="align-item-center m-3 m-lg-0">
                    <Dropdown
                      isOpen={dropdownStatusOpen}
                      toggle={this.toggleSelectStatus}
                      className="list-invoice__dropdown"
                    >
                      <DropdownToggle caret>
                        {dropdownStatusName}
                      </DropdownToggle>
                      <DropdownMenu>
                        <DropdownItem
                          value=""
                          name="status"
                          onClick={(e) =>
                            this.handleChangeFilterStatus(e, "All")
                          }
                        >
                          All
                        </DropdownItem>
                        {cons.BILLING_STATUS_INFO.map((item) => {
                          return (
                            <DropdownItem
                              key={item.id}
                              name="status"
                              onClick={(e) =>
                                this.handleChangeFilterStatus(
                                  e,
                                  `${item.status}`
                                )
                              }
                              value={item.id}
                            >
                              {item.status}
                            </DropdownItem>
                          );
                        })}
                      </DropdownMenu>
                    </Dropdown>

                    <ButtonGroup
                      className="btn-group-toggle"
                      data-toggle="buttons"
                    >
                      {/* <Button
                        className="bg-transparent"
                        color="default"
                        outline
                        onClick={this.filter}
                      >
                        <span>Filter</span>
                        <i className="fas fa-filter fa-xs"></i>
                      </Button> */}

                      <Button
                        className="bg-transparent"
                        color="default"
                        outline
                        onClick={this.clearFilterData}
                      >
                        <span>Clear Filters</span>
                        <i className="fas fa-eraser"></i>
                      </Button>
                    </ButtonGroup>
                  </Row>
                </Col>
                <Col
                  className="d-flex justify-content-center justify-content-xl-end mt-lg-3 mt-xl-0"
                  xl={4}
                  lg={12}
                  xs={12}
                >
                  <Row className="align-items-center">
                    <Form onSubmit={this.onSearch} className="mr-5">
                      <Input
                        type="text"
                        name="search"
                        placeholder="Search..."
                      />
                    </Form>

                    <Button
                      className="mr-lg-3"
                      color="primary"
                      size="sm"
                      onClick={this.exportCSV}
                      disabled={
                        bookingInvoiceArr && bookingInvoiceArr.length === 0
                      }
                    >
                      <span className="btn-inner--text">Export</span>
                    </Button>

                    {/* <div className="d-flex align-items-center">
                      <ReactDatetime
                        className="custom-date-input trigger-date"
                        inputProps={{
                          placeholder: "-- cron job date --",
                        }}
                        closeOnSelect={true}
                        value={this.state.trigger_date}
                        timeFormat={false}
                        dateFormat={"MMM Do YYYY"}
                        onChange={(e) =>
                          this.handleReactDatetimeChange("trigger_date", e)
                        }
                        isValidDate={this.state.validateTriggerDate}
                      ></ReactDatetime>
                      <Button
                        className="m-3 ml-lg-1"
                        color="primary"
                        size="sm"
                        onClick={this.triggerCronJobChargeInvoice}
                        disabled={this.state.isDisabledTriggerBtn}
                      >
                        <span className="btn-inner--text">Trigger</span>
                      </Button>
                    </div>
                    <div className="d-flex align-items-center">
                      <small className="text-muted mr-3">
                        {" "}
                        Display void invoices
                      </small>
                      <label className="custom-toggle mr-1">
                        <input
                          type="checkbox"
                          onChange={this.handleFilterVoidBilling}
                          checked={this.state.checkVoidStatus}
                        />
                        <span
                          className="custom-toggle-slider rounded-circle"
                          data-label-off="All"
                          data-label-on="Void"
                        />
                      </label>
                    </div> */}
                  </Row>
                </Col>
              </Row>
            </CardHeader>
          </Card>
          <Card>
            <CardBody className="container-table-billing">
              <Table className="align-items-center table-sort" responsive>
                <thead className="thead-light">
                  <tr>
                    <th
                      className={`text-center ${sortClass.get("created_at")}`}
                      name="created_at"
                      onClick={this.sort}
                    >
                      {" "}
                      Invoice Date{" "}
                    </th>
                    <th className="text-center"> Invoice number </th>
                    <th
                      className={`text-center ${sortClass.get(
                        "provider__first_name"
                      )}`}
                      name="provider__first_name"
                      onClick={this.sort}
                    >
                      {" "}
                      Provider{" "}
                    </th>

                    <th
                      className={`table__header__w-170 text-center ${sortClass.get(
                        "status"
                      )}`}
                      name="status"
                      onClick={this.sort}
                    >
                      Status
                    </th>
                    <th
                      className={classNames(
                        "text-center",
                        sortClass.get("total_amount")
                      )}
                      name="total_amount"
                      onClick={this.sort}
                    >
                      Total
                    </th>
                    <th className="table__header__w-95" />
                  </tr>
                </thead>
                <tbody className="client">
                  {bookingInvoiceArr &&
                    bookingInvoiceArr.map((item, i) => {
                      return (
                        <BookingInvoice
                          key={i}
                          check={this.handleCheckbox}
                          openSideBar={this.toggleSideBar}
                          item={item}
                          setStatus={this.getProviderUpdateStatus}
                          handleUpdateStatus={this.handleUpdateStatus}
                          handleSendInvoice={this.handleSendInvoice}
                          toggleAction={this.toggleAction}
                        />
                      );
                    })}
                </tbody>
              </Table>
            </CardBody>
            {bookingInvoiceArr && bookingInvoiceArr.length > 0 && (
              <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mx-0">
                <Col className="d-flex justify-content-center">
                  {totalPage > 1 && (
                    <PaginationBar
                      setting={paginationSetting}
                      handleClick={this.selectPage}
                    />
                  )}
                </Col>
                <Col
                  className="d-flex align-items-baseline mb-md-0 mb-2"
                  xs="auto"
                >
                  <Input
                    onChange={this.selectPageSize}
                    value={limit}
                    bsSize="md"
                    type="select"
                    name="selectPageSize"
                    id="selectPageSize"
                  >
                    <option value="5">5</option>
                    <option value="10">10</option>
                    <option value="20">20</option>
                    <option value="50">50</option>
                  </Input>
                  <Label className="ml-1 text-nowrap">
                    <small>
                      Page {`${selected + 1}`} of {`${totalPage}`}
                    </small>
                  </Label>
                </Col>
              </Row>
            )}
          </Card>
          {/* <div className="my-3">
            <Col md={6}>
              <Link to="/admin/billings/create-invoice">
                <Button color="info"> Create Invoice</Button>
              </Link>
            </Col>
          </div> */}
        </Col>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    bookingInvoiceData: state.billing.data,
    loading: state.billing.loading,
  };
};

const mapActionToProps = { getList, updateStatus, triggerCronJob };

export default connect(mapStateToProps, mapActionToProps)(ListBookingInvoice);
