import React, { useState } from "react";
import { Link } from "react-router-dom";
import {
  Badge,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import {
  getDateTime,
  getUserFullName,
  getStatusInfo,
  fixNumber,
} from "helpers/UtilsHelper";
import cons from "../../../constants";

const BookingInvoice = (props) => {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const toggleAction = () => setDropdownOpen(!dropdownOpen);
  const { item, handleUpdateStatus, handleSendInvoice } = props;
  const fullName = getUserFullName(item.provider);
  const status = getStatusInfo(cons.BILLING_STATUS_INFO, item.status);
  return (
    <tr className="invoice">
      <td className="text-center">
        {getDateTime(item.createdAt, "MMM DD,YYYY HH:mm:ss")}
      </td>
      <td className="text-center">
        <Link to={`/admin/billings/booking-invoice/detail/${item.id}`}>
          #{item.invoiceNumber}
        </Link>
      </td>
      <td className="text-center">{fullName}</td>
      {/* <td className="text-center">
        {getDateTime(item.dueDate, "MMM DD,YYYY HH:mm:ss")}
      </td> */}
      <td className="text-center">
        <Badge
          color={status ? status.color : "danger"}
          className="badge-dot mr-4"
        >
          <i className={`bg-${status ? status.color : "danger"}`} />
          <span className="status">{status ? status.status : "Unknown"}</span>
        </Badge>
      </td>
      <td className="table-actions text-center">
        {fixNumber(item.totalAmount)}
      </td>
      <td>
        <ButtonDropdown isOpen={dropdownOpen} toggle={toggleAction}>
          <DropdownToggle color="link" className="dropdown-customs">
            <i className="fas fa-ellipsis-v fa-lg" />
          </DropdownToggle>
          <DropdownMenu className="invoice__action">
            <DropdownItem onClick={(e) => handleSendInvoice(item.id)}>
              <b className="text-muted">Send</b>
              <i className="far fa-paper-plane ml-2 text-muted" />
            </DropdownItem>
            <DropdownItem divider />
            <DropdownItem onClick={(e) => handleUpdateStatus(2, item.id)}>
              <b className="text-danger">Void</b>
              <i className="fas fa-ban ml-2 text-danger" />
            </DropdownItem>
            <DropdownItem divider />
            <DropdownItem onClick={(e) => handleUpdateStatus(0, item.id)}>
              <b className="text-info">Paid</b>
              <i className="far fa-check-square ml-2 text-info" />
            </DropdownItem>
          </DropdownMenu>
        </ButtonDropdown>
      </td>
    </tr>
  );
};

export default BookingInvoice;
